import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

import { GalleryList } from '../cmps/Gallery/GalleryList';
import { Loading } from '../cmps/Loading';

import { ArtService } from '../services/ArtService';

function Gallery() {
    const [imgs, setImgs] = useState();
    const [artists, setArtists] = useState();
    const [isMounting, setIsMounting] = useState(false);
    const [selectedClassHandler, setSelectedClassHandler] = useState(true);

    const showItemsHandler = (key) => {
        if ((key === 'artists' && artists) || (key === 'imgs' && imgs)) return;
        setSelectedClassHandler(!selectedClassHandler);
        if (key === 'artists') {
            setImgs(null);
            setTimeout(() => setArtists(ArtService.getArtists()), 1000);
            setIsMounting(!isMounting);
        } else {
            setArtists(null);
            setTimeout(() => setImgs(ArtService.query()), 1000);
            setIsMounting(!isMounting);
        }
    }

    useEffect(() => {
        setTimeout(() => setImgs(ArtService.query()), 1000);
        setIsMounting(!isMounting);
        return (() => setIsMounting(!isMounting))
    }, [])

    return (
        <div className="flex column full rtl gallery-container">
            <div className="flex main-container w100 show-container">
                <div>הצג לפי:</div>
                <div className={!selectedClassHandler ? 'selected' : 'pointer'} onClick={() => showItemsHandler('artists')}>אמנים</div>
                <div>|</div>
                <div className={selectedClassHandler ? 'selected' : 'pointer'} onClick={() => showItemsHandler('imgs')}>יצירות</div>
            </div>
            <SwitchTransition mode="out-in">
                <CSSTransition
                    key={isMounting}
                    timeout={600}
                    classNames={'cmp-animation'}
                >
                    {imgs ?
                        <>
                            <div className="main-container gallery-view-container">
                                <GalleryList props={imgs} type={'imgs'} />
                            </div>
                            <Link className="flex main-container w100 home-button-container" to={'/'}>
                                <button>לעמוד הבית</button>
                            </Link>
                        </> :
                        artists ?
                            <>
                                <div className="main-container gallery-view-container">
                                    <GalleryList props={artists} type={'artists'} />
                                </div>
                                <Link className="flex main-container w100 home-button-container" to={'/'}>
                                    <button>לעמוד הבית</button>
                                </Link>
                            </> :
                            <Loading />}
                </CSSTransition>
            </SwitchTransition>
        </div>
    )
}

export { Gallery };