import React from 'react';
import { Link } from 'react-router-dom';

function ArtistPreview({ prop }) {

    return (
        <Link to={`/artist/${prop._id}`} className="flex column artist-preview-container">
            <img src={require(`../../assets/artistsImg/${prop.imgId}.jpg`)} alt="" />
            <div>{prop.name}</div>
        </Link>
    )
}

export { ArtistPreview };