import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { ArtService } from '../services/ArtService';

function ArtistDetails(props) {

    const { id } = props.match.params;
    const [artist, setArtist] = useState(ArtService.getArtistById(id));
    const [arts, setArts] = useState(ArtService.getItemsByName(artist.name));

    useEffect(() => {
        setArtist(ArtService.getArtistById(id));
        if (artist) setArts(ArtService.getItemsByName(artist.name));
    }, [id])

    return (
        <div className="flex column full rtl main-container w100 artist-container">
            {artist &&
                <>
                    <div className="flex artist-info-container">
                        <img src={require(`../assets/artistsImg/${artist.imgId}.jpg`)} alt="" />
                        <div className="flex column">
                            <div>{artist.name}</div>
                            <div>{artist.about}</div>
                        </div>
                    </div>
                    <div className="seperator-container">היצירות שמוצגות בתערוכה:</div>
                    <div className="arts-container">
                        {arts.map(art => {
                            return (
                                <Link to={`/gallery/${art._id}`} key={art._id}>
                                    <img src={art.imgUrl} />
                                </Link>
                            )
                        })}
                    </div>
                </>
            }
        </div>
    )
}

export { ArtistDetails }