import React from 'react';
import { Link } from 'react-router-dom';

function ImagePreview({ prop }) {

    const desc = prop.description.substring(0, 15).concat('...');

    return (
        <Link to={`/gallery/${prop._id}`} className="flex column image-preview-container">
            <img src={prop.imgUrl} alt={desc} />
            <div className="flex center align-center image-preview-container-screen">{prop.author}</div>
        </Link>
    )
}

export { ImagePreview };